.header {
  background-color: white;
  &__container {
    nav {
      height: 80px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
  }
  &__logo {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    &:hover {
      text-decoration: none;
    }
    a {
      color: $color-black;
      &:hover {
        text-decoration: none;
      }
    }
  }
  &__logo-apple {
    color: $color-black;
    letter-spacing: -0.73px;
    font-family: $pt-root-regular;
    font-size: 24px;
    margin-left: 20px;
  }
  &__logo-fixit {
    color: $color-black;
    font-family: $pt-root-medium;
    font-size: 24px;
    letter-spacing: -0.73px;
    margin-left: 5px;
  }
  &__menu {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 0;
    @media (max-width: 991px) {
      display: none;
    }
    li {
      margin-left: 60px;
      list-style-type: none;
      color: $color-black;
      font-family: $pt-root-regular;
      font-size: 21px;
      letter-spacing: -0.2px;
      &:first-child {
        margin-left: 0;
      }
    }
    a {
      color: $color-black;
      transition: 0.3s;
      &:hover {
        color: #0091ff;
      }
    }
    &__current {
      a {
        font-family: $pt-root-medium;
        color: #0091ff !important;
      }
      font-family: $pt-root-medium;
      color: #0091ff !important;
    }
  }
  &__buttons {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    @media (max-width: 991px) {
      display: none;
    }
  }
  &__instagram {
    margin-right: 40px;
    padding: 10px;
    border: 2px solid $color-blue;
    transition: 0.3s;
    background-position: center;
    img {
      opacity: 1;
      transition: 0.3s;
    }
    &:hover {
      border: 2px solid #007ad7;
      background-color: #007ad7;
      background-image: url("images/instagram-white.svg");
      background-repeat: no-repeat;
      background-position: center;
      img {
        opacity: 0;
      }
    }
  }
  &__call {
    border: 2px solid $color-blue;
    height: 50px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 15px 25px;
    transition: 0.3s;
    img {
      opacity: 0;
    }
    &:hover {
      border: 2px solid #007ad7;
      background-color: #007ad7;
      color: white;
      .header__call-icon {
        background-image: url("images/call-white.svg");
      }
    }
    &:focus {
      border: 2px solid #0168b7;
      background-color: #0168b7;
      color: white;
      img {
        opacity: 0;
      }
    }
  }
  &__call-icon {
    background-image: url("images/call-blue.svg");
    background-repeat: no-repeat;
    transition: 0.3s;
    margin-left: 15px;
  }
  &__hamburger {
    height: 35px;
    cursor: pointer;
    display: none;
    @media (max-width: 991px) {
      display: inline-block;
    }
  }
  &__mobile {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    right: -100%;
    background-color: $color-white-background;
    transition: 0.3s;
    z-index: 200;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    &__active {
      right: 0;
    }
    &-menu {
      padding-left: 0;
      margin-top: -70px;
      @media (max-height: 630px) {
        margin-top: -90px;
      }
      li {
        list-style-type: none;
        color: $color-black;
        font-family: $pt-root-regular;
        font-size: 21px;
        letter-spacing: -0.2px;
        text-align: center;
        margin-bottom: 20px;
        &:first-child {
          margin-left: 0;
        }
      }
      a {
        color: $color-black;
      }
    }
  }
  &__buttons-mobile {
    position: absolute;
    bottom: 100px;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @media (max-height: 630px) {
      bottom: 50px;
    }
    .header {
      &__call {
        width: 237px;
        margin-top: 25px;
      }
      &__instagram {
        margin-right: 0;
      }
    }
  }
  &__close {
    cursor: pointer;
    position: absolute;
    height: 35px;
    right: 25px;
    top: 25px;
  }
}



