/**
 * ============
 * Typography
 * ============
 * Define as much reusable typography here
 * such as `.text-hero`, which can be `@extend`-ed from other scss files
 * This way, this file become a master list of typography used throughout the site
 */

$pt-root-regular: "PT RootUI Regular", sans-serif;
$pt-root-medium: "PT RootUI Medium", sans-serif;
$pt-root-bold: "PT RootUI Bold", sans-serif;
$pt-caption-bold: "PT Sans Caption Bold", sans-serif;

@font-face {
  font-family: "PT RootUI Regular";
  src: url("../fonts/PTRootUI-Regular.eot") format("eot"), url("../fonts/PTRootUI-Regular.woff") format("woff"), url("../fonts/PTRootUI-Regular.ttf") format("truetype"), url("../fonts/PTRootUI-Regular.woff2") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "PT RootUI Medium";
  src: url("../fonts/PTRootUI-Medium.eot") format("eot"), url("../fonts/PTRootUI-Medium.woff") format("woff"), url("../fonts/PTRootUI-Medium.ttf") format("truetype"), url("../fonts/PTRootUI-Medium.woff2") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "PT RootUI Bold";
  src: url("../fonts/PTRootUI-Bold.eot") format("eot"), url("../fonts/PTRootUI-Bold.woff") format("woff"), url("../fonts/PTRootUI-Bold.ttf") format("truetype"), url("../fonts/PTRootUI-Bold.woff2") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "PT Sans Caption Bold";
  src: url("../fonts/PTSans-CaptionBold.eot") format("eot"), url("../fonts/PTSans-CaptionBold.woff") format("woff"), url("../fonts/PTSans-CaptionBold.ttf") format("truetype"), url("../fonts/PTSans-CaptionBold.woff2") format("woff");
  font-weight: normal;
  font-style: normal;
}



