      margin-bottom: 40px;
    }
    p {
      color: #272727;
      font-family: $pt-root-regular;
      font-size: 18px;
      line-height: 28px;
      text-align: center;
      margin-bottom: 66px;
      @media (max-width: 767px) {
        margin-bottom: 50px;
      }
    }
    a {
      color: white;
      font-family: $pt-root-bold;
      font-size: 21px;
      background-color: $color-blue;
      width: 287px;
      height: 62px;
      margin: 0 auto;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      transition: 0.3s;
      &:hover {
        background-color: #007ad7;
      }
      &:focus {
        background-color: #0168b7;
      }
    }
  }
}
