.product {
  margin-top: 32px;
  margin-bottom: 140px;
  @media (max-width: 991px) {
    margin-top: 30px;
    margin-bottom: 100px;
  }
  @media (max-width: 767px) {
    margin-top: 30px;
    margin-bottom: 75px;
  }
  &__container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: relative;
    @media (max-width: 767px) {
      flex-direction: column;
    }
  }
  &__left {
    width: 50%;
    @media (max-width: 767px) {
      width: 100%;
      height: 500px;
    }
  }
  &__image {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    @media (max-width: 767px) {
      align-items: flex-start;
    }
    img {
      position: absolute;
      width: 100%;
      max-width: 365px;
      opacity: 0;
      transition: 0.3s;
      @media (max-width: 991px) {
        max-width: 300px;
      }
    }
  }
  &__show {
    img {
      opacity: 1;
    }
  }
  &__right {
    width: 50%;
    @media (max-width: 767px) {
      width: 100%;
    }
  }
  form {
    h1 {
      color: #232323;
      font-family: $pt-root-bold;
      font-size: 44px;
      letter-spacing: -0.06px;
      @media (max-width: 991px) {
        font-size: 38px;
      }
      @media (max-width: 767px) {
        font-size: 32px;
      }
    }
    h3 {
      margin-top: 52px;
      margin-bottom: 17px;
      color: #232323;
      font-family: $pt-root-medium;
      font-size: 16px;
      width: 100%;
      @media (max-width: 991px) {
        margin-top: 30px;
      }
    }
  }
  &__section {
    display: flex;
    flex-wrap: wrap;
  }
  &__color {
    position: relative;
    display: inline-block;
    input {
      position: absolute;
      width: 100%;
      height: 100%;
      visibility: hidden;
      border: 1px solid transparent;
    }
    input[type="radio"]:checked + label {
      border: 1px solid #e0e0e0;
    }
    label {
      display: inline-flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      padding: 11px 32px;
      background-color: #fafafa;
      border: 1px solid transparent;
      transition: 0.3s;
      color: #232323;
      font-family: $pt-root-medium;
      font-size: 16px;
      cursor: pointer;
      margin-right: 16px;
      margin-bottom: 16px;
    }
  }
  &__circle {
    width: 40px;
    height: 40px;
    background-color: #4a4a4a;
    border-radius: 50%;
    margin-right: 18px;
  }
  &__memory {
    position: relative;
    input {
      position: absolute;
      width: 100%;
      height: 100%;
      visibility: hidden;
      border: 1px solid transparent;
    }
    input[type="radio"]:checked + label {
      border: 1px solid #e0e0e0;
    }
    label {
      display: inline-flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      padding: 11px 32px;
      background-color: #fafafa;
      border: 1px solid transparent;
      transition: 0.3s;
      color: #232323;
      font-family: $pt-root-medium;
      font-size: 18px;
      text-transform: uppercase;
      cursor: pointer;
      margin-right: 16px;
      margin-bottom: 16px;
      @media (max-width: 767px) {
        font-size: 16px;
      }
    }
  }
  &__bottom {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    @media (max-width: 991px) {
      flex-direction: column-reverse;
    }
    @media (max-width: 767px) {
      align-items: center;
    }
  }
  &__price {
    margin-left: 50px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    color: #232323;
    font-family: $pt-root-medium;
    font-size: 32px;
    letter-spacing: -0.04px;
    @media (max-width: 991px) {
      margin-top: 30px;
      margin-left: 0;
      margin-bottom: 20px;
      font-size: 38px;
    }
    @media (max-width: 767px) {
      font-size: 32px;
    }
  }
}
.item {
  .infoblock__i {
    margin-bottom: 56px;
    @media (max-width: 991px) {
      margin-top: 45px;
    }
    @media (max-width: 767px) {
      margin-bottom: 30px;
    }
  }
  .infoblock__required {
    margin-bottom: 167px;
    @media (max-width: 991px) {
      margin-bottom: 120px;
    }
    @media (max-width: 767px) {
      margin-bottom: 80px;
    }
  }
}



