.popup {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 1000;
  right: 0;
  left: 0;
  bottom: 0;
  top: 0;
  opacity: 0;
  transition: 0.3s;
  &__inner {
    height: auto;
    h2 {
      text-align: center;
    }
    h3 {
      text-align: center;
    }
    &__submited {
      visibility: hidden;
      height: 0;
    }
  }
  &__block {
    position: relative;
    padding: 0 15px;
    width: 100%;
  }
  &__active {
    visibility: visible;
    z-index: 1000;
    opacity: 1;
    .popup__inner {
      position: relative;
    }
  }
  &__radio {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    &-item {
      position: relative;
      margin-left: 24px;
      &:first-child {
        margin-left: 0;
      }
    }
    input {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
      opacity: 0;
      transition: 0.3s;
      border: 1px solid transparent;
    }
    label {
      margin-bottom: 0;
      cursor: pointer;
      border: 1px solid transparent;
      transition: 0.3s;
    }
    input[type="radio"]:checked + label {
      border: 1px solid #e0e0e0;
    }
  }
  &__phone {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: center;
    transition: 0.3s;
    input {
      width: 300px;
      height: 70px;
      border: 1px solid #ececec;
      background-color: #fafafa;
      outline: none;
      color: #232323;
      font-family: $pt-root-medium;
      font-size: 24px;
      letter-spacing: 0.13px;
      text-align: center;
    }
    input:hover {
      &::placeholder {
        opacity: 0.52;
      }
    }
    input:focus::placeholder {
      color: transparent;
    }
    input:focus {
      padding-left: 44px;
      text-align: left;
    }
    input::placeholder {
      opacity: 0.31;
      color: #232323;
      font-family: $pt-root-medium;
      font-size: 24px;
      letter-spacing: 0.13px;
      transition: 0.3s;
    }
  }
  &__close {
    cursor: pointer;
    position: absolute;
    right: 80px;
    top: 80px;
    width: 20px;
    height: 20px;
    display: block;
    &:hover {
      cursor: pointer;
    }
  }
  &__close:after,
  &__close:before {
    content: "";
    position: absolute;
    width: 2px;
    height: 1.5em;
    background: #ccc;
    display: block;
    transform: rotate(45deg);
    margin: -3px 0 0 -1px;
    right: 8px;
  }
  &__close:hover:after,
  &__close:hover:before {
    background: #aaa;
  }

  &__close:before {
    transform: rotate(-45deg);
  }
  &__error {
    input {
      background-color: #fff4f4;
      border: 1px solid #f56e72;
    }
  }
}
.popup-order {
  .popup {
    &__block {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      text-align: center;
      max-width: 670px;
      background-color: white;
      padding: 60px;
      h2 {
        color: #232323;
        font-family: $pt-root-medium;
        font-size: 32px;
        letter-spacing: -0.04px;
        margin-bottom: 0;
      }
      h3 {
        margin-top: 50px;
        margin-bottom: 15px;
        color: #232323;
        font-family: $pt-root-medium;
        font-size: 18px;
      }
    }
    &__close {
      right: 50px;
      top: 70px;
      @media (max-width: 420px) {
        top: 30px;
      }
    }
    &__phone {
      margin-bottom: 56px;
    }
    &__submited {
      visibility: hidden;
      opacity: 0;
      height: 0;
      transition: 0.3s;
      &__active {
        visibility: visible;
        opacity: 1;
        height: auto;
      }
    }
    &__accepted {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      text-align: center;
      h2 {
        margin-bottom: 35px;
      }
      p {
        font-family: $pt-root-regular;
        font-size: 18px;
        line-height: 29px;
        padding-bottom: 0;
        margin-top: 35px;
      }
    }
  }
}
