.footer {
  background-color: #111117;
  padding: 35px 80px 25px 80px;
  @media (max-width: 1200px) {
    padding: 35px 0 25px 0;
  }
  @media (max-width: 991px) {
    padding: 5px 15px 25px 15px;
  }
  @media (max-width: 767px) {
    padding: 5px 0 25px 0;
  }
  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    @media (max-width: 991px) {
      flex-direction: column-reverse;
    }
  }
  &__left {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @media (max-width: 991px) {
      margin-top: 25px;
      flex-wrap: wrap;
      flex-direction: row;
    }
    @media (max-width: 575px) {
      justify-content: center;
      flex-direction: column;
    }
    p {
      margin-bottom: 0;
    }
  }
  &__developing {
    @media (max-width: 991px) {
      text-align: right;
    }
  }
  &__company-name {
    a {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      @media (max-width: 575px) {
        justify-content: center;
      }
    }
  }
  &__fixit {
    color: white;
    font-family: $pt-root-medium;
    font-size: 32px;
    letter-spacing: -0.74px;
    margin-left: 7px;
  }
  &__apple {
    color: white;
    font-family: $pt-root-regular;
    font-size: 32px;
    letter-spacing: -0.74px;
  }
  &__copyright {
    color: white;
    font-family: $pt-root-regular;
    font-size: 16px;
    margin-bottom: 20px;
    @media (max-width: 991px) {
      margin-bottom: 0;
    }
    @media (max-width: 575px) {
      text-align: center;
    }
  }
  &__split {
    color: white;
    font-family: $pt-root-regular;
    font-size: 16px;
    img {
      margin-top: -1px;
      @media (max-width: 991px) {
        margin-top: 5px;
      }
      @media (max-width: 575px) {
        margin-top: 10px;
      }
    }
    @media (max-width: 991px) {
      width: 100%;
    }
    @media (max-width: 575px) {
      text-align: center;
    }
  }
  &__right {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    @media (max-width: 991px) {
      justify-content: space-between;
    }
    @media (max-width: 575px) {
      flex-direction: column;
      align-items: center;
    }
  }
  &__contact-column {
    margin-left: 80px;
    @media (max-width: 1300px) {
      margin-left: 40px;
    }
    @media (max-width: 991px) {
      margin-left: 25px;
      &:first-child {
        margin-left: 0;
      }
    }
    @media (max-width: 767px) {
      width: 30%;
    }
    @media (max-width: 575px) {
      margin-left: 0;
      width: auto;
    }
    h3 {
      opacity: 0.72;
      color: white;
      font-family: $pt-root-medium;
      font-size: 16px;
      margin-bottom: 17px;
      text-align: right;
      margin-top: 25px;
      @media (max-width: 991px) {
        text-align: left;
      }
      @media (max-width: 575px) {
        text-align: center;
      }
    }
    p {
      color: white;
      font-family: $pt-root-regular;
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 0;
      text-align: right;
      @media (max-width: 991px) {
        text-align: left;
      }
      @media (max-width: 575px) {
        text-align: center;
      }
    }
    a {
      color: white;
      font-family: $pt-root-regular;
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 0;
      text-align: right;
      @media (max-width: 991px) {
        text-align: left;
      }
      @media (max-width: 575px) {
        text-align: center;
      }
    }
  }
  &__tel {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    @media (max-width: 991px) {
      justify-content: flex-start;
    }
    @media (max-width: 575px) {
      text-align: center;
    }
  }
}



