.shop {
  .shop-radio {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    &__container {
      padding: 0;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      @media (max-width: 991px) {
        flex-wrap: wrap;
      }
    }
    &__item {
      display: inline-flex;
      width: 100%;
      height: 72px;
      margin-right: 10px;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      background-color: #fafafa;
      border: 1px solid transparent;
      transition: 0.3s;
      color: #232323;
      font-family: $pt-root-medium;
      font-size: 16px;
      cursor: pointer;
      margin-bottom: 16px;
      min-width: 185px;
      @media (max-width: 991px) {
        margin-right: 0;
      }
      &:last-child {
        margin-right: 0;
      }
      &:hover {
        border: 1px solid #ceeaff;
        background-color: #ceeaff;
        color: #0091ff;
        .shop-radio__icon {
          img {
            &:first-child {
              opacity: 0;
            }
            &:last-child {
              opacity: 1;
            }
          }
        }
      }
    }
    &__icon {
      position: relative;
      height: 46px;
      width: 35px;
      margin-right: 27px;
      @media (max-width: 1200px) {
        margin-right: 15px;
      }
      img {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        margin: auto;
        max-height: 46px;
        &:first-child {
          opacity: 1;
          transition: 0.3s;
        }
        &:last-child {
          opacity: 0;
          transition: 0.3s;
        }
      }
    }
  }
  .stock {
    position: relative;
    h2 {
      text-align: center;
      color: #232323;
      font-size: 32px;
      font-weight: 700;
      letter-spacing: -0.04px;
      font-family: $pt-root-bold;
      margin: 60px 0;
      @media (max-width: 991px) {
        margin: 40px 0;
      }
    }
    &__section {
      position: relative;
      opacity: 1;
      min-height: 408px;
      max-height: none;
      z-index: 1;
      &.shop-radio__active {
        position: relative;
        opacity: 1;
        min-height: 408px;
        max-height: none;
        z-index: 1;
      }
    }
    &__container {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      flex-wrap: wrap;
      max-width: 1330px;
      @media (max-width: 1330px) {
        max-width: 1005px;
      }
      @media (max-width: 1005px) {
        max-width: 680px;
      }
      @media (max-width: 680px) {
        max-width: 100%;
        justify-content: center;
      }
    }
    &__item {
      width: 300px;
      height: 410px;
      border: 1px solid #f3f3f3;
      margin: 12px;
      position: relative;
      transition: 0.3s;
      a {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: center;
      }
      &:hover {
        box-shadow: 0 2px 15px #f6f4f4;
      }
    }
    &__title {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: center;
      overflow: hidden;
      h3 {
        color: #232323;
        font-family: $pt-root-bold;
        font-size: 21px;
        font-weight: 700;
        letter-spacing: -0.03px;
        width: 100%;
        text-align: center;
        margin-top: 15px;
        margin-bottom: 15px;
        @media (max-width: 991px) {
          font-size: 18px;
        }
      }
      p {
        color: #232323;
        font-family: $pt-root-medium;
        font-size: 18px;
        letter-spacing: -0.02px;
        text-align: center;
        margin-bottom: 25px;
        padding-bottom: 0;
        @media (max-width: 991px) {
          font-size: 18px;
        }
      }
    }
  }
  .infoblock {
    &__repair {
      margin-top: 65px;
      margin-bottom: 53px;
      @media (max-width: 991px) {
        margin-top: 45px;
        margin-bottom: 30px;
      }
      @media (max-width: 767px) {
        margin-top: 30px;
      }
      .infoblock__container {
        padding: 56px 90px;
        @media (max-width: 991px) {
          padding: 30px;
        }
        @media (max-width: 767px) {
          padding: 25px;
        }
      }
    }
    &__i {
      margin-top: 80px;
      margin-bottom: 56px;
      @media (max-width: 991px) {
        margin-top: 50px;
        margin-bottom: 30px;
      }
      @media (max-width: 767px) {
        margin-top: 30px;
      }
    }
    &__required {
      margin-bottom: 45px;
      @media (max-width: 991px) {
        margin-bottom: 30px;
      }
    }
  }
}



