.repair-top {
  background-color: #fbfbfb;
  h1 {
    text-align: center;
    color: #232323;
    font-family: $pt-root-bold;
    font-size: 44px;
    letter-spacing: -0.06px;
    padding: 50px 0;
  }
  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    @media (max-width: 767px) {
      flex-direction: column;
    }
  }
  &__left {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 50%;
    @media (max-width: 767px) {
      width: 100%;
    }
  }
  &__right {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 650px;
    @media (max-width: 767px) {
      width: 100%;
      margin-top: 26px;
      min-height: 50px;
    }
  }
  &__panel {
    background-color: white;
    margin-bottom: 8px;
    &:first-child {
      .repair-top__heading {
        &:after {
          content: "";
        }
      }
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__heading {
    padding: 0 32px;
    background-color: white;
    height: 80px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    position: relative;
    border-bottom: 1px solid #ededed;
    cursor: pointer;
    @media (max-width: 767px) {
      padding: 0 20px;
    }
    @media (max-width: 575px) {
      padding: 0 15px;
    }
    h3 {
      margin-right: 30px;
      margin-bottom: 0;
      @media (max-width: 767px) {
        font-size: 24px;
      }
      @media (max-width: 575px) {
        font-size: 22px;
      }
    }
    &:after {
      content: url("images/arrow-down-small.svg");
      right: 32px;
      position: absolute;
      top: 0;
      bottom: 0;
      margin-bottom: auto;
      margin-top: auto;
      height: 12px;
      transition: 0.3s;
      cursor: pointer;
      @media (max-width: 767px) {
        right: 20px;
      }
      @media (max-width: 575px) {
        right: 15px;
      }
    }
  }
  &__collapse {
    background-color: white;
    max-height: 0;
    transition: 0.3s;
    visibility: hidden;
    opacity: 0;
  }
  &__show {
    .repair-top__collapse {
      max-height: none;
      visibility: visible;
      opacity: 1;
    }
    .repair-top__heading {
      &:after {
        transform: rotate(180deg);
      }
    }
  }
  &__table-item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
    padding: 0 45px;
    @media (max-width: 767px) {
      padding: 0 20px;
    }
    @media (max-width: 575px) {
      padding: 0 15px;
    }
    &:first-child {
      padding-top: 32px;
    }
    &:last-child {
      margin-bottom: 0;
      padding-bottom: 42px;
    }
    h4 {
      color: #4a4a4a;
      font-family: $pt-root-regular;
      font-size: 18px;
      margin-bottom: 0;
    }
    p {
      color: #0593ff;
      font-family: $pt-root-bold;
      font-size: 24px;
      font-weight: 700;
      margin-bottom: 0;
      padding-left: 10px;
      text-align: end;
      min-width: 140px;
    }
  }
}
.repairitem {
  background-color: #fbfbfb;
  .infoblock__i {
    margin-top: 80px;
    margin-bottom: 40px;
    @media (max-width: 991px) {
      margin-top: 50px;
    }
    @media (max-width: 767px) {
      margin-top: 30px;
      margin-bottom: 30px;
    }
  }
  .infoblock__required {
    margin-bottom: 60px;
    @media (max-width: 991px) {
      margin-bottom: 45px;
    }
  }
}
.item-description {
  margin-top: 35px;
  p {
    color: #232323;
    font-family: $pt-root-regular;
    font-size: 16px;
  }
}
