.container {
  max-width: 1285px;
}
a {
  &:hover {
    text-decoration: none;
  }
}
button {
  color: white;
  font-family: $pt-root-bold;
  font-size: 21px;
  background-color: $color-blue;
  min-width: 287px;
  padding: 17px 40px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 300px;
  transition: 0.3s;
  outline: none;
  border: 1px solid transparent;
  &:hover {
    background-color: #007ad7;
    outline: none;
  }
  &:focus {
    background-color: #0168b7;
    outline: none;
  }
  @media (max-width: 767px) {
    font-size: 19px;
    min-width: 250px;
    padding: 10px 10px;
  }
}
.progress {
  position: relative;
  overflow: hidden;
  progress {
    position: absolute;
    visibility: hidden;
  }
}
#accordion {
  .progress {
    position: absolute;
    overflow: hidden;
    width: 100%;
    left: 0;
    right: 0;
    bottom: 0;
  }
}
.progress-bg {
  background: #e5f7ff;
  position: relative;
  height: 8px;
  border-radius: 0;
  overflow: hidden;
}
.progress-bar {
  overflow: hidden;
  background: $color-blue;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
}

.accordion {
  .progress-bar {
    &__zero {
      animation-name: makezero;
      animation-duration: 0.1s;
    }
    &__animate {
      animation-duration: 20s;
      animation-name: movebar;
      animation-timing-function: linear;
    }
  }
}

.top-slider {
  .progress-bar {
    &__zero {
      animation-name: makezero;
      animation-duration: 0.1s;
    }
    &__animate {
      animation-duration: 3.4s;//duration of the slider
      animation-name: movebar;
      animation-timing-function: linear;
    }
  }
}

@keyframes movebar {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes makezero {
  to {
    width: 0;
  }
}

.slick-slide {
  height: auto !important;
  max-width: 100%;
  img {
    max-width: 100%;
  }
}




