.info {
  background-color: white;
  max-height: 500px;
  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0;
    @media (max-width: 767px) {
      flex-direction: column;
    }
  }
  &__text {
    background-color: $color-white-background;
    width: 50%;
    margin-right: 8px;
    padding: 43px 65px;
    @media (max-width: 991px) {
      padding: 20px 25px;
    }
    @media (max-width: 767px) {
      width: 100%;
      margin-right: 0;
    }
    @media (max-height: 800px) {
      padding: 20px 40px;
    }
  }
  &__address {
    h3 {
      color: #2a2a2a;
      font-family: $pt-root-bold;
      font-size: 21px;
      margin-bottom: 15px;
      @media (max-width: 991px) {
        font-size: 18px;
      }
    }
    p {
      color: #2a2a2a;
      font-family: $pt-root-regular;
      font-size: 21px;
      line-height: 34px;
      margin-bottom: 0;
      @media (max-width: 991px) {
        font-size: 18px;
        line-height: 22px;
      }
    }
  }
  &__schedule {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    h3 {
      width: 100%;
      color: #2a2a2a;
      font-family: $pt-root-bold;
      font-size: 21px;
      margin-top: 47px;
      @media (max-width: 991px) {
        font-size: 18px;
        margin-top: 30px;
      }
    }
    &-left {
      color: #2a2a2a;
      font-family: $pt-root-medium;
      font-size: 21px;
      p {
        margin-bottom: 5px;
        @media (max-width: 991px) {
          font-size: 18px;
        }
      }
    }
    &-right {
      color: #2a2a2a;
      font-family: $pt-root-regular;
      font-size: 21px;
      margin-left: 37px;
      p {
        margin-bottom: 5px;
        @media (max-width: 991px) {
          font-size: 18px;
        }
      }
    }
  }
  &__phones {
    @media (max-width: 767px) {
      width: 100%;
    }
    h3 {
      color: #2a2a2a;
      font-family: $pt-root-bold;
      font-size: 21px;
      margin-top: 42px;
      @media (max-width: 991px) {
        font-size: 18px;
        margin-top: 30px;
      }
    }
    a {
      color: #2a2a2a;
      font-family: $pt-root-regular;
      font-size: 21px;
      margin-right: 25px;
      &:last-child {
        margin-right: 0;
      }
      @media (max-width: 991px) {
        font-size: 18px;
      }
    }
  }
  &__map {
    width: 50%;
    height: auto;
    margin-left: 8px;
    @media (max-width: 767px) {
      width: 100%;
      height: 400px;
      margin-top: 15px;
      margin-left: 0;
    }
  }
}
.contact {
  .infoblock {
    margin-top: 24px;
    margin-bottom: 170px;
    @media (max-width: 1200px) {
      margin-bottom: 135px;
    }
    @media (max-width: 991px) {
      margin-bottom: 120px;
    }
    @media (max-width: 767px) {
      margin-top: 20px;
      margin-bottom: 100px;
    }
    @media (max-width: 575px) {
      margin-bottom: 70px;
    }
  }
}



