.infoblock {
  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: $color-white-background;
    padding: 40px;
    background-repeat: no-repeat;
    background-position-x: 20px;
    background-size: auto 100%;
    @media (max-width: 991px) {
      padding: 30px;
    }
    @media (max-width: 767px) {
      padding: 25px;
      flex-direction: column;
    }
  }
  &__text {
    @media (max-width: 767px) {
      margin-bottom: 6px;
    }
    h3 {
      color: #232323;
      font-family: $pt-root-medium;
      font-size: 32px;
      letter-spacing: -0.04px;
      @media (max-width: 767px) {
        font-size: 28px;
        text-align: center;
      }
      @media (max-width: 575px) {
        font-size: 25px;
      }
    }
    h4 {
      color: #232323;
      font-family: $pt-root-regular;
      font-size: 21px;
      letter-spacing: -0.23px;
      @media (max-width: 767px) {
        text-align: center;
        font-size: 19px;
      }
    }
    p {
      color: #272727;
      font-family: $pt-root-regular;
      font-size: 18px;
      line-height: 28px;
      margin-bottom: 0;
      @media (max-width: 767px) {
        font-size: 15px;
        line-height: 23px;
      }
    }
  }
  &__button {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 300px;
    @media (max-width: 767px) {
      width: 250px;
    }
    a {
      color: white;
      font-family: $pt-root-bold;
      font-size: 21px;
      background-color: $color-blue;
      min-width: 287px;
      padding: 17px 40px;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      transition: 0.3s;
      &:hover {
        background-color: #007ad7;
      }
      &:focus {
        background-color: #0168b7;
      }
      @media (max-width: 767px) {
        font-size: 19px;
        min-width: 250px;
        padding: 10px 10px;
      }
    }
  }
  &__i {
    img {
      position: absolute;
      left: 5px;
      right: 0;
      top: 0;
      bottom: 0;
      margin-top: auto;
      margin-bottom: auto;
      @media (max-width: 767px) {
        position: relative;
        margin-top: -10px;
        margin-bottom: 10px;
      }
    }
    h4 {
      margin-left: 70px;
      margin-bottom: 0;
      @media (max-width: 767px) {
        margin-left: 0;
      }
    }
    .infoblock__text {
      position: relative;
      @media (max-width: 767px) {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
    }
  }
}



