.repair {
  .stock {
    &__container {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      flex-wrap: wrap;
      max-width: 1265px;
      @media (max-width: 1265px) {
        max-width: 1020px;
      }
      @media (max-width: 1020px) {
        max-width: 775px;
      }
      @media (max-width: 775px) {
        max-width: 525px;
      }
      @media (max-width: 525px) {
        max-width: 100%;
        justify-content: center;
      }
    }
    &__item {
      width: 223px;
      height: 387px;
      border: 1px solid #f3f3f3;
      margin: 12px;
      position: relative;
      transition: 0.3s;
      a {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: center;
        height: 100%;
        position: relative;
      }
      &:hover {
        box-shadow: 0 2px 15px #f6f4f4;
      }
    }
    &__image {
      img {
        position: absolute;
        left: 0;
        right: 0;
        margin: auto;
        transition: 0.3s;
        bottom: 80px;
        &:first-child {
          opacity: 1;
        }
      }
    }
    &__title {
      h3 {
        color: #232323;
        font-family: $pt-root-bold;
        font-size: 21px;
        font-weight: 700;
        letter-spacing: -0.03px;
        width: 100%;
        text-align: center;
        margin-bottom: 34px;
        margin-top: 15px;
      }
    }
  }
  .infoblock__status {
    margin-top: 65px;
    @media (max-width: 991px) {
      margin-top: 50px;
    }
    @media (max-width: 767px) {
      margin-top: 30px;
    }
    @media (max-width: 575px) {
      margin-top: 15px;
    }
  }
  .infoblock__repair {
    margin-top: 55px;
    outline: none;
    h3 {
      margin-bottom: 30px;
    }
    @media (max-width: 991px) {
      margin-top: 50px;
      h3 {
        margin-bottom: 20px;
      }
    }
    @media (max-width: 767px) {
      margin-top: 30px;
      h3 {
        margin-bottom: 10px;
      }
    }
  }
  .infoblock__i {
    margin-top: 32px;
    margin-bottom: 32px;
  }
  .infoblock__required {
    margin-top: 143px;
    margin-bottom: 48px;
    @media (max-width: 991px) {
      margin-top: 80px;
      margin-bottom: 30px;
    }
    @media (max-width: 767px) {
      margin-top: 30px;
    }
  }
  iframe {
    bottom: 0;
    left: calc(100vw - 185px) !important;
    top: 80px !important;
    button {
      background-color: #0091ff;
      border-radius: 0;
      font-family: $pt-root-bold;
    }
  }
  button {
    background-color: #0091ff;
    border-radius: 0;
    font-family: $pt-root-bold;
  }
}



