.info {
  background-color: white;
  .accordion {
    background-color: white;
    &__container {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      padding: 0;
    }
    &__titles {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      width: 100%;
      max-width: 100%;
      margin-right: 0;
    }
    &__panel {
      background-color: white;
      padding: 0 0 0 0;
      margin-top: 15px;
      position: relative;
      overflow: hidden;
      &:first-child {
        margin-top: 0;
      }
    }
    &__heading {
      color: $color-black;
      font-family: $pt-root-medium;
      font-size: 21px;
      position: relative;
      background-color: #f6f4f4;
      cursor: pointer;
      @media (max-width: 1200px) {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        margin: 0 auto;
      }
      &:after {
        content: url("images/arrow-down.svg");
        right: 56px;
        position: absolute;
        top: 0;
        bottom: 0;
        margin-bottom: auto;
        margin-top: auto;
        height: 21px;
        transition: 0.3s;
        cursor: pointer;
        @media (max-width: 991px) {
          width: 40px;
        }
        @media (max-width: 767px) {
          width: 28px;
        }
        @media (max-width: 575px) {
          width: 25px;
        }
      }
      img {
        width: 36px;
        position: absolute;
        left: 45px;
        right: 0;
        top: 0;
        bottom: 0;
        margin-top: auto;
        margin-bottom: auto;
        @media (max-width: 1200px) {
          position: relative;
          left: 15px;
        }
        @media (max-width: 767px) {
          width: 33px;
        }
        @media (max-width: 575px) {
          width: 30px;
        }
      }
      a {
        color: $color-black;
      }
      h3 {
        cursor: pointer;
        padding-bottom: 45px;
        padding-top: 45px;
        max-width: 1000px;
        margin: 0 auto;
        color: #232323;
        font-family: $pt-root-bold;
        font-size: 32px;
        letter-spacing: -0.04px;
        margin-right: 80px;
        @media (max-width: 1200px) {
          margin-left: 30px;
          padding-bottom: 40px;
          padding-top: 40px;
        }
        @media (max-width: 767px) {
          font-size: 28px;
          padding-bottom: 30px;
          padding-top: 30px;
        }
        @media (max-width: 575px) {
          font-size: 25px;
        }
      }
      &__info-body {
        img {
          width: 58px;
          left: 26px;
          @media (max-width: 1200px) {
            left: -5px;
          }
          @media (max-width: 767px) {
            left: 0;
            width: 53px;
          }
          @media (max-width: 575px) {
            width: 50px;
          }
        }
      }
    }
    &__collapse {
      color: $color-black;
      font-family: $pt-root-regular;
      font-size: 18px;
      line-height: 28px;
      transition: 0.3s;
      max-height: 0;
      overflow: hidden;
      background-color: #fbfbfb;
      width: 100%;
      opacity: 0;
      h3 {
        color: #4a4a4a;
        font-family: $pt-root-medium;
        font-size: 28px;
        background-color: #fbfbfb;
        max-width: 1000px;
        margin: 0 auto;
        padding-top: 45px;
        padding-bottom: 25px;
        @media (max-width: 991px) {
          font-size: 22px;
          margin-left: 20px;
          margin-right: 20px;
        }
      }
    }
    &__table {
      background-color: white;
      max-width: 1000px;
      margin: 0 auto;
      padding: 50px 40px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      @media (max-width: 991px) {
        flex-direction: column;
        margin-left: 20px;
        margin-right: 20px;
        padding: 30px 20px;
      }
      &:last-child {
        margin-bottom: 100px;
      }
    }
    &__table-column {
      max-width: 50%;
      @media (max-width: 991px) {
        max-width: 100%;
        &:first-child {
          margin-bottom: 40px;
        }
      }
    }
    &__table-item {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-bottom: 42px;
      &:last-child {
        margin-bottom: 0;
      }
      h4 {
        color: #4a4a4a;
        font-family: $pt-root-medium;
        font-size: 24px;
        margin-bottom: 0;
        width: 270px;
        @media (max-width: 991px) {
          font-size: 22px;
          width: 220px;
        }
        @media (max-width: 767px) {
          font-size: 20px;
          width: 200px;
        }
        @media (max-width: 575px) {
          width: 180px;
        }
      }
      p {
        color: $color-blue;
        font-family: $pt-root-medium;
        font-size: 24px;
        margin-bottom: 0;
        padding-left: 10px;
        @media (max-width: 991px) {
          font-size: 22px;
        }
        @media (max-width: 767px) {
          font-size: 20px;
        }
      }
    }
    &__show {
      .accordion__heading {
        &:after {
          transform: rotate(180deg);
          margin-right: -10px;
        }
      }
      .accordion__collapse {
        max-height: none;
        opacity: 1;
      }
    }
    &__title-fixit {
      font-family: $pt-root-medium;
    }
  }
  .info-title-problem {
    margin-top: 55px;
    margin-bottom: 60px;
    @media (max-width: 767px) {
      margin-top: 45px;
      margin-bottom: 45px;
    }
    h2 {
      text-align: center;
      color: #262626;
      font-family: $pt-root-bold;
      font-size: 32px;
    }
  }
  .infoblock__status {
    margin-top: 65px;
    margin-bottom: 55px;
    @media (max-width: 767px) {
      margin-top: 30px;
      margin-bottom: 30px;
    }
  }
  .infoblock__i {
    margin-bottom: 40px;
  }
  .infoblock__question {
    margin-top: 55px;
    margin-bottom: 45px;
    @media (max-width: 767px) {
      margin-top: 30px;
    }
  }
}



